/* eslint-disable max-len, no-param-reassign */
import SparkMD5 from 'spark-md5';
import t from 'tcomb-validation';
import logger from 'logger';
import throttle from 'lodash.throttle';
import { checkOptions, snakeKeys } from './utils';

const log = logger.context('api-client');
const uploadURL = ENV.uploadApiUrl;

const parseStoreOpts = (session, opts) => {
  const allowed = [
    { name: 'location', type: t.enums.of('s3 gcs rackspace azure dropbox') },
    { name: 'region', type: t.String },
    { name: 'path', type: t.String },
    { name: 'container', type: t.String },
    { name: 'access', type: t.enums.of('public private') },
  ];

  // Throw if options are invalid
  checkOptions('upload (storeParams)', allowed, opts);

  // Convert to format expected by API
  const snakeOpts = snakeKeys(opts, 'store');

  if (session.policy && session.signature) {
    return Object.assign({}, snakeOpts, { signature: session.signature, policy: session.policy });
  }

  return snakeOpts;
};

const Uploader = function Uploader(session, config, storeParams) {
  function md5Base64(data) {
    return btoa(SparkMD5.ArrayBuffer.hash(data, true));
  }

  this.config = Object.assign({}, {
    host: uploadURL,
    apikey: session.apikey,
    partSize: 5 * 1024 * 1024,
    maxConcurrentUploads: 5,
    cryptoMd5Method: md5Base64,
    retryOptions: {
      retries: 10, // max number of retries
      factor: 2, // the exponential factor to use
      minTimeout: 1 * 1000, // milliseconds before starting first retry
      maxTimeout: 60 * 1000, // max ms between two retries
    }, // this default config will retry for about 6 mins
    onStart: null,
    onRetry: null,
    onUploadStart: null,
    onProgress: null,
    onPaused: null,
    onUploadCompleteFunction: null,
    onCompleteFunction: null,
    onUploadFailedFunction: null,
  }, config);

  if (storeParams !== undefined) {
    this.storeParams = Object.assign({}, {
      store_location: '',
      store_region: '',
      store_container: '',
      store_path: '',
      store_access: '',
    }, parseStoreOpts(session, storeParams));
  }

  this.parts = {};
  this.file = null;
  this.currentUploads = 0;
  this.uploadData = null;
  this.uploadFailed = null;
  this.attempts = 0; // number of retries
  this.cancelRequested = false;
  this.pauseRequested = false;
};

Uploader.prototype.slice = File.prototype.slice
  || File.prototype.mozSlice || File.prototype.webkitSlice;

Uploader.prototype.multipartUpload = function multipartUpload(file) {
  const u = this;

  u.file = file;
  u.parts.reader = new FileReader();
  u.parts.current = 0;
  u.parts.total = Math.ceil(u.file.size / u.config.partSize);
  u.parts.uploaded = [];
  u.parts.failures = [];

  if (u.config.onProgress !== null) {
    u.parts.progress = Array(...Array(u.parts.total)).map(Number.prototype.valueOf, 0);
  }

  u.parts.reader.onload = function onload(e) {
    u.onPartLoad(e);
  };

  u.initUpload();
};

// Initiate multipart upload.
Uploader.prototype.initUpload = function initUpload() {
  const u = this;
  u.start((err, res) => {
    if (res && res.ok) {
      // Only start if initial start call was accepted
      u.loadNextPart();
    } else {
      u.processRetries();
    }
  });
};

Uploader.prototype.start = function start(callback) {
  const u = this;

  if (u.config.onStart !== null) {
    u.config.onStart();
  }

  const dataFields = {
    filename: u.file.newName || u.file.name,
    // TODO Find a better way to determine default
    mimetype: u.file.type || 'application/octet-stream',
    size: u.file.size,
  };

  const formData = u.createUploadFormData(dataFields);
  const xhr = new XMLHttpRequest();

  xhr.onreadystatechange = function onreadystatechange() {
    if (xhr.readyState === 4) {
      if (xhr.status === 200) {
        const res = {
          ok: true,
        };
        u.uploadData = JSON.parse(xhr.response);
        callback(null, res);
      } else {
        const msg = xhr.response || 'no response. Please check your browser network log for more info';
        u.config.onUploadFailedFunction(new Error(`Upload "start" failed with response: ${msg}`));
      }
    }
  };

  xhr.onerror = function onerror(e) {
    callback(e, null);
  };

  xhr.open('POST', `${u.config.host}/multipart/start`, true);
  xhr.send(formData);
};

Uploader.prototype.loadNextPart = function loadNextPart() {
  const u = this;

  // Don't keep reading / processing unless there is an open slot
  if (u.pauseRequested || (u.currentUploads >= u.config.maxConcurrentUploads)) {
    setTimeout(() => { u.loadNextPart(); }, 100);
    return;
  }
  u.currentUploads += 1; // take a slot

  const start = u.parts.current * u.config.partSize;
  const end = ((start + u.config.partSize) >= u.file.size)
                ? u.file.size
                : start + u.config.partSize;

  u.parts.reader.readAsArrayBuffer(u.slice.call(u.file, start, end));
};

Uploader.prototype.onPartLoad = function onPartLoad(e) {
  const u = this;
  log('Loading part: ', u.parts.current);

  // b/c of retries we have to check if this part has already been
  // succesfully uploaded, if so, loadNextPart()
  // we do this here to avoid threading timing issues
  let i = 0;
  let skipPart = false;
  for (i = 0; i < u.parts.uploaded.length; i += 1) {
    const partArray = u.parts.uploaded[i].split(':', 1);
    const part = parseInt(partArray[0], 10);
    if (part === (u.parts.current + 1)) {
      skipPart = true;
      break;
    }
  }

  u.parts.current += 1;
  if (!(skipPart || u.cancelRequested)) {
    const md5 = u.config.cryptoMd5Method(e.target.result);
    u.getUploadData(e.target.result, u.parts.current, e.total, md5);
  } else {
    u.handleError(new Error('Upload cancelled'), u.parts.current, e.total);
  }

  if (u.parts.current < u.parts.total) {
    u.loadNextPart();
  } else {
    u.waitForAllPartsAttempted();
  }
};

Uploader.prototype.getUploadData = function getUploadData(arrayBuffer, part, size, md5) {
  const u = this;

  if (u.uploadFailed !== null) {
    u.config.onUploadFailedFunction(new Error('Upload failed loading a chunk. Please check your network log.'));
    return;
  }

  // Wait for response from multipart start request.
  if (u.uploadData === null) {
    setTimeout(() => { u.getUploadData(arrayBuffer, part, size, md5); }, 100);
    return;
  }

  const dataFields = {
    part,
    size,
    md5,
  };

  const formData = u.createUploadFormData(dataFields);

  const xhr = new XMLHttpRequest();

  xhr.onreadystatechange = function onreadystatechange() {
    if (xhr.readyState === 4) {
      if (xhr.status === 200) {
        u.uploadNextPart(arrayBuffer, JSON.parse(xhr.response), part);
      }
    }
  };

  xhr.onerror = function onerror(e) {
    u.handleError(e, part, arrayBuffer.byteLength);
  };

  xhr.open('POST', `${u.config.host}/multipart/upload`, true);
  xhr.send(formData);
};

// Returns true if all parts have been successfully loaded
Uploader.prototype.allPartsLoaded = function allPartsLoaded() {
  const u = this;
  return (u.parts.uploaded.length === u.parts.total);
};

Uploader.prototype.uploadNextPart = function uploadNextPart(arrayBuffer, uploadData, part) {
  const u = this;
  u.uploadPart(arrayBuffer, uploadData, part);
};

// Note: this function uses XHR instead of super-agent b/c super-agent seems to have
// a bug with Content-Length header when using this from mocha tests
Uploader.prototype.uploadPart = function uploadPart(arrayBuffer, uploadData, part) {
  const u = this;

  if (u.config.onUploadStart !== null) {
    u.config.onUploadStart();
    // Set onUploadStart to null to fire it only once.
    u.config.onUploadStart = null;
  }

  const xhr = new XMLHttpRequest();
  xhr.onreadystatechange = function onreadystatechange() {
    if (xhr.readyState === 4) {
      if (xhr.status === 200) {
        u.currentUploads -= 1;
        const etag = xhr.getResponseHeader('ETag');
        u.parts.uploaded.push(`${part}:${etag}`);
      }
    }
  };

  if (u.config.onProgress !== null) {
    const throttled = throttle((data) => {
      u.updateProgress(data.loaded, part, arrayBuffer.byteLength);
      // user requested to cancel the current upload
      if (u.cancelRequested) {
        xhr.abort();
        u.handleError(new Error('Upload cancelled'), part, arrayBuffer.byteLength);
      }
    }, 1000);
    xhr.upload.onprogress = throttled;
  }

  xhr.onerror = function onerror(e) {
    u.handleError(e, part, arrayBuffer.byteLength);
  };

  xhr.open('PUT', uploadData.url, true);
  Object.keys(uploadData.headers).forEach((key) => {
    xhr.setRequestHeader(key, uploadData.headers[key]);
  });

  // Uint8Array gets rid of a safari warning about arrayBuffer
  // being deprecated as part of xmlhttpreq
  xhr.send(new Uint8Array(arrayBuffer));
};

Uploader.prototype.updateProgress = function updateProgress(amount, part, partLength) {
  const u = this;

  if (u.config.onProgress !== null) {
    u.parts.progress[part] = amount;

    // Progress is total progress thus far
    const progressTotal = u.parts.progress.reduce((a, b) => {
      return a + b;
    }, 0);

    const totalProgressPercent = Math.round((progressTotal / u.file.size) * 100);
    u.config.onProgress({
      totalProgressPercent,   // Percent of total upload
      progressTotal,          // Total number of bytes uploaded thus far across all parts
      part,                   // Part #
      loaded: amount,                // Amount of data in this part that has been uploaded
      byteLength: partLength, // Total number of bytes in this part
    });
  }
};

// an error happened during upload
Uploader.prototype.handleError = function handleError(e, part, partLength) {
  const u = this;
  log('handleError: ', part);

  // make sure this error hasn't already been handled
  if (u.parts.failures.indexOf(part) > -1) return;
  u.updateProgress(0, part, partLength); // reset progress amount for this part
  u.currentUploads -= 1; // open up slot for next part
  u.parts.failures.push(part);
};

Uploader.prototype.waitForAllPartsAttempted = function waitForAllPartsAttempted() {
  const u = this;
  if ((u.parts.uploaded.length + u.parts.failures.length) < u.parts.total) {
    setTimeout(() => { u.waitForAllPartsAttempted(); }, 100);
    return;
  }

  if (u.allPartsLoaded()) {
    u.complete();
  } else {
    u.processRetries();
  }
};

Uploader.prototype.backOffWait = function backOffWait() {
  const u = this;
  return (u.attempts === 0) ? u.config.retryOptions.minTimeout : Math.min(
    u.config.retryOptions.maxTimeout,
    1000 * (u.config.retryOptions.factor ** u.attempts));
};

Uploader.prototype.processRetries = function processRetries() {
  const u = this;

  if (!u.cancelRequested &&
    u.config.retryOptions &&
    (u.attempts < u.config.retryOptions.retries)) {
    log('processRetries called');
    const backOffWait = u.backOffWait();
    // reset counters and try again
    u.currentUploads = 0;
    u.parts.current = 0;
    u.parts.failures = [];
    u.attempts += 1; // increment number of failed attempts

    if (u.config.onRetry !== null) {
      u.config.onRetry(u.attempts, backOffWait);
    }

    if (u.uploadData) {
      // call to start succeeded, but one or more parts failed
      setTimeout(() => { u.loadNextPart(); }, backOffWait);
    } else {
      // call to start didn't even work
      setTimeout(() => { u.initUpload(); }, backOffWait);
    }
  } else if (u.cancelRequested) {
    u.config.onUploadFailedFunction(new Error('Upload cancelled'));
  } else {
    // reached max retries
    u.uploadFailed = true;
    u.config.onUploadFailedFunction(new Error(`Failed to upload after ${u.attempts} attempt(s).`));
  }
};

Uploader.prototype.complete = function complete() {
  const u = this;

  if (u.config.onUploadCompleteFunction !== null) {
    u.config.onUploadCompleteFunction();
  }

  const dataFields = {
    size: u.file.size,
    filename: u.file.newName || u.file.name,
    // TODO Find a better way to determine default
    mimetype: u.file.type || 'application/octet-stream',
    parts: u.parts.uploaded.join(';'),
  };

  const formData = u.createUploadFormData(dataFields);

  const xhr = new XMLHttpRequest();

  xhr.onreadystatechange = function onreadystatechange() {
    if (xhr.readyState === 4) {
      if (xhr.status === 200) {
        if (u.config.onCompleteFunction !== null) {
          u.config.onCompleteFunction(JSON.parse(xhr.response));
        }
      } else {
        const msg = xhr.response || 'no response. Please check your browser network log for more info';
        u.config.onUploadFailedFunction(new Error(`Upload "complete" failed with API response: ${msg}`));
      }
    }
  };

  xhr.onerror = function onerror(e) {
    u.config.onUploadFailedFunction(new Error(`Upload "complete" failed with network error: ${e.message}`));
  };

  xhr.open('POST', `${u.config.host}/multipart/complete`, true);
  xhr.send(formData);
};

Uploader.prototype.createUploadFormData = function createUploadFormData(extraData) {
  const u = this;
  const formData = new FormData();

  formData.append('apikey', u.config.apikey);

  if (u.storeParams !== null) {
    Object.keys(u.storeParams).forEach((key) => {
      if (u.storeParams[key] !== '') {
        formData.append(key, u.storeParams[key]);
      }
    });
  }

  if (u.uploadData !== null) {
    Object.keys(u.uploadData).forEach((key) => {
      formData.append(key, u.uploadData[key]);
    });
  }

  if (extraData) {
    Object.keys(extraData).forEach((key) => {
      formData.append(key, extraData[key]);
    });
  }

  return formData;
};

Uploader.prototype.cancel = function cancel() {
  log('upload.cancel() called');
  const u = this;
  u.uploadFailed = true;
  u.cancelRequested = true;
};

// Pausing waits for all current parts to complete before actually pausing
Uploader.prototype.waitForAllPartsPaused = function waitForAllPartsPaused() {
  const u = this;
  if (u.currentUploads !== 0) {
    setTimeout(() => { u.waitForAllPartsPaused(); }, 100);
    return;
  }

  // make sure they didn't click cancel or some part failed before
  // calling back to onPaused
  if ((u.config.onPaused !== null) && !u.uploadFailed) {
    u.config.onPaused();
  }
};

Uploader.prototype.pause = function pause() {
  log('upload.pause() called');
  const u = this;
  u.pauseRequested = true;
  if (u.config.onPaused !== null) {
    setTimeout(() => { u.waitForAllPartsPaused(); }, 100);
  }
};

Uploader.prototype.resume = function resume() {
  log('upload.resume() called');
  const u = this;
  u.pauseRequested = false;
};

const upload = (session, file, options = {}, storeOptions = {}, token = {}) => {
  log('cloud.upload() called:', file);
  return new Promise((resolve, reject) => {
    const defaults = Object.assign({}, options, {
      onCompleteFunction: res => resolve(res),
      onUploadFailedFunction: err => reject(err),
    });
    const uploader = new Uploader(session, defaults, storeOptions);
    uploader.multipartUpload(file);

    token.cancel = () => {
      uploader.cancel();
    };

    token.pause = () => {
      uploader.pause();
    };

    token.resume = () => {
      uploader.resume();
    };
  });
};

export default upload;
