import request from 'superagent';
import t from 'tcomb-validation';
import { checkOptions, removeEmpty } from './utils';

const storeApiUrl = ENV.storeApiUrl;

const storeURL = (session, url, options = {}) => {
  if (!url || typeof url !== 'string') {
    throw new Error('url is required for store');
  }
  const allowed = [
    { name: 'filename', type: t.String },
    { name: 'location', type: t.enums.of('s3 gcs rackspace azure dropbox') },
    { name: 'mimetype', type: t.String },
    { name: 'path', type: t.String },
    { name: 'region', type: t.String },
    { name: 'container', type: t.String },
    { name: 'access', type: t.enums.of('public private') },
  ];

  // Throw if options are invalid
  checkOptions('store', allowed, options);

  // Default location
  const location = options.location || 's3';

  const newOptions = removeEmpty(options);

  if (session.policy && session.signature) {
    newOptions.policy = session.policy;
    newOptions.signature = session.signature;
  }

  const baseURL = `${storeApiUrl}/${location}`;

  return new Promise((resolve, reject) => {
    request
      .post(baseURL)
      .query({ key: session.apikey })
      .query(newOptions)
      .send(`url=${url}`)
      .end((err, res) => {
        if (err) {
          reject(err);
        } else if (res.body && res.body.url) {
          const handle = res.body.url.split('/').pop();
          const response = Object.assign({}, res.body, { handle });
          resolve(response);
        } else {
          resolve(res.body);
        }
      });
  });
};

export default storeURL;
