/* eslint-disable no-multi-spaces */
import request from 'superagent';
import t from 'tcomb-validation';
import { checkOptions, removeEmpty, snakeKeys } from './utils';

const fileApiUrl = ENV.fileApiUrl;

const retrieve = (session, handle, options = {}) => {
  if (!handle || typeof handle !== 'string') {
    throw new Error('handle is required for retrieve');
  }
  const allowed = [
    { name: 'metadata', type: t.Boolean },
    { name: 'head', type: t.Boolean },
    { name: 'cache', type: t.Boolean },
    { name: 'dl', type: t.Boolean },
    { name: 'extension', type: t.String },
  ];

  // Throw if options are invalid
  checkOptions('retrieve', allowed, options);

  let baseURL = `${fileApiUrl}/${handle}`;
  const newOptions = removeEmpty(options);

  // extension is added to handle like 'DCL5k45342323kadsf+name.jpg'
  // it is not part of query string
  if (newOptions.extension) {
    baseURL += `+${newOptions.extension}`;
    delete newOptions.extension;
  }

  // metadata is passed as 'DCL5k45342323kadsf/metadata'
  if (newOptions.metadata) {
    baseURL += '/metadata';
  }

  if (session.policy && session.signature) {
    newOptions.policy = session.policy;
    newOptions.signature = session.signature;
  }

  if (newOptions.head) {
    delete newOptions.head;
    return new Promise((resolve, reject) => {
      request
        .head(baseURL)
        .query(newOptions)
        .end((err, res) => {
          if (err) {
            reject(err);
          } else {
            resolve(res.headers);
          }
        });
    });
  }
  return new Promise((resolve, reject) => {
    const responseType = (newOptions.metadata) ? 'json' : 'blob';
    delete newOptions.metadata;

    request
      .get(baseURL)
      .query(newOptions)
      .responseType(responseType)
      .end((err, res) => {
        if (err) {
          reject(err);
        } else {
          resolve(res.body);
        }
      });
  });
};

const remove = (session, handle) => {
  if (!handle || typeof handle !== 'string') {
    throw new Error('handle is required for remove');
  }

  if (!session.policy || !session.signature) {
    throw new Error('security policy and signature are required for remove');
  }

  const baseURL = `${fileApiUrl}/${handle}`;

  return new Promise((resolve, reject) => {
    request
      .delete(baseURL)
      .query({ key: session.apikey })
      .query({ policy: session.policy, signature: session.signature })
      .end((err, res) => {
        if (err) {
          reject(err);
        } else {
          resolve(res.body);
        }
      });
  });
};

const metadata = (session, handle, options = {}) => {
  if (!handle || typeof handle !== 'string') {
    throw new Error('handle is required for metadata');
  }
  const allowed = [
    { name: 'size',       type: t.Boolean },
    { name: 'mimetype',   type: t.Boolean },
    { name: 'filename',   type: t.Boolean },
    { name: 'width',      type: t.Boolean },
    { name: 'height',     type: t.Boolean },
    { name: 'uploaded',   type: t.Boolean },
    { name: 'writeable',  type: t.Boolean },
    { name: 'cloud',      type: t.Boolean },
    { name: 'sourceUrl',  type: t.Boolean },
    { name: 'md5',        type: t.Boolean },
    { name: 'sha1',       type: t.Boolean },
    { name: 'sha224',     type: t.Boolean },
    { name: 'sha256',     type: t.Boolean },
    { name: 'sha384',     type: t.Boolean },
    { name: 'sha512',     type: t.Boolean },
    { name: 'location',   type: t.Boolean },
    { name: 'path',       type: t.Boolean },
    { name: 'container',  type: t.Boolean },
    { name: 'exif',       type: t.Boolean },
  ];

  // Throw if options are invalid
  checkOptions('retrieve', allowed, options);

  const newOptions = removeEmpty(snakeKeys(options));

  if (session.policy && session.signature) {
    newOptions.policy = session.policy;
    newOptions.signature = session.signature;
  }

  const baseURL = `${fileApiUrl}/${handle}/metadata`;
  return new Promise((resolve, reject) => {
    request
      .get(baseURL)
      .query(newOptions)
      .end((err, res) => {
        if (err) {
          reject(err);
        } else {
          resolve(res.body);
        }
      });
  });
};

export {
  retrieve,
  remove,
  metadata,
};
