/* eslint-disable no-param-reassign */
import t from 'tcomb-validation';
import snake from 'snake-case';

const validate = t.validate;

export const checkOptions = (name, allowed, options) => {
  const keys = Object.keys(options);
  const allowedNames = allowed.map(a => a.name);
  const namesFormatted = allowedNames.join(', ');
  keys.forEach((key) => {
    if (allowedNames.indexOf(key) < 0) {
      throw new Error(`${key} is not a valid option for ${name}. Valid options are: ${namesFormatted}`);
    }
  });
  allowed.forEach((obj) => {
    let value = options[obj.name];
    if (obj.name === 'location' && typeof value === 'string') {
      value = value.toLowerCase();
    }
    if (value) {
      const result = validate(value, obj.type);
      if (!result.isValid()) {
        throw new Error(result.firstError().message);
      }
    }
  });
  return keys;
};

export const removeEmpty = (obj) => {
  const newObj = Object.assign({}, obj);
  Object.keys(newObj).forEach(k => (!newObj[k] && newObj[k] !== undefined) && delete newObj[k]);
  return newObj;
};

export const snakeKeys = (opts, prefix = '') => {
  const options = Object.assign({}, opts);
  return Object.keys(options)
    .reduce((prev, curr) => {
      prev[snake(`${prefix}-${curr}`)] = options[curr];
      delete options[curr];
      return prev;
    }, {});
};
