/* eslint-disable no-param-reassign */
import logger from 'logger';
import request from 'superagent';
import t from 'tcomb-validation';
import { checkOptions, removeEmpty, snakeKeys } from './utils';

const log = logger.context('api-client');
const cloudApiUrl = ENV.cloudApiUrl;

export default (session, security, cloudName) => {
  const params = { apikey: session.apikey };
  if (security && security.policy && security.signature) {
    params.policy = security.policy;
    params.signature = security.signature;
  }
  const list = (path, token = {}) => {
    log('cloud.list() called:', path);
    return new Promise((resolve, reject) => {
      const req = request
        .get(`${cloudApiUrl}/${cloudName}/folder/list${path}`)
        .query(params)
        .withCredentials()
        .end((err, response) => {
          if (err) {
            reject(err);
          } else {
            log('cloud.list() responded:', response.body);
            resolve(response.body);
          }
        });
      token.cancel = () => {
        req.abort();
        reject(new Error('Cancelled'));
      };
    });
  };

  const store = (path, options = {}, token = {}) => {
    log('cloud.store() called:', path, options);

    // TODO Add security options here
    const allowed = [
      { name: 'location', type: t.enums.of('s3 gcs rackspace azure dropbox') },
      { name: 'region', type: t.String },
      { name: 'path', type: t.String },
      { name: 'container', type: t.String },
      { name: 'access', type: t.enums.of('public private') },
    ];

    // Throw if options are inalid
    checkOptions('cloud.store', allowed, options);

    // Convert to format expected by API
    const snakeOpts = snakeKeys(options, 'store');

    return new Promise((resolve, reject) => {
      const req = request
        .get(`${cloudApiUrl}/${cloudName}/store${path}`)
        .query(params)
        .query(removeEmpty(snakeOpts))
        .withCredentials()
        .end((err, response) => {
          if (err) {
            reject(err);
          } else {
            log('cloud.store() responded:', response.body);
            resolve(response.body);
          }
        });

      token.cancel = () => {
        req.abort();
        reject(new Error('Cancelled'));
      };
    });
  };

  const link = (path, token = {}) => {
    log('cloud.link() called:', path);
    return new Promise((resolve, reject) => {
      const req = request
        .get(`${cloudApiUrl}/${cloudName}/link${path}`)
        .query(params)
        .withCredentials()
        .end((err, response) => {
          if (err) {
            reject(err);
          } else {
            log('cloud.link() responded:', response.body);
            resolve(response.body);
          }
        });
      token.cancel = () => {
        req.abort();
        reject(new Error('Cancelled'));
      };
    });
  };

  const logout = (name) => {
    log('cloud.logout() called:', name);
    const baseURL = name ? `${cloudApiUrl}/${name}` : cloudApiUrl;
    return new Promise((resolve, reject) => {
      request
        .get(`${baseURL}/auth/logout`)
        .query({ apikey: session.apikey })
        .withCredentials()
        .end((err, response) => {
          if (err) {
            reject(err);
          } else {
            log('cloud.logout() responded:', response.body);
            resolve(response.body);
          }
        });
    });
  };

  return {
    name: cloudName,
    list,
    store,
    link,
    logout,
  };
};
