/* eslint-disable max-len */
/* eslint-disable no-param-reassign */

import t from 'tcomb-validation';
import snake from 'snake-case';
import { checkOptions } from './utils';

const processURL = ENV.processApiUrl;

/*
-----------------------------------
Type definitions
-----------------------------------
*/

// Number range (not integer range)
const numberRange = (start, end) => {
  return t.refinement(t.Number, n => n >= start && n <= end);
};

// Integer range type
const min = int => t.refinement(t.Integer, n => n >= int);
const max = int => t.refinement(t.Integer, n => n <= int);
const range = (start, end) => {
  return t.tuple([min(start), max(end)], 'range');
};

// Special type for resize align
const alignment = t.enums.of('top left right bottom');
const alignPair = t.refinement(t.tuple([alignment, alignment]), tup => tup[0] !== tup[1], 'pair');

/*
-----------------------------------
Format options for API
-----------------------------------
*/

const formatOption = (op, options) => {
  const name = snake(op.name);
  const value = options[op.name];
  if (Array.isArray(value)) {
    return `${name}:[${value}]`;
  }
  return value ? `${name}:${value}` : null;
};

const formatOptions = (name, allowed, options) => {
  const fOptions = allowed
    .map(op => formatOption(op, options))
    .filter(op => op)
    .join(',');

  const snakeName = snake(name);

  if (fOptions.length) {
    return `${snakeName}=${fOptions}`;
  }
  return snakeName;
};

/*
-----------------------------------
Transform options
-----------------------------------
*/

const sepia = (transforms = [], options) => {
  const allowed = [
    { name: 'tone', type: numberRange(0, 100) },
  ];
  checkOptions('sepia', allowed, options);
  const sepiaOptions = formatOptions('sepia', allowed, options);
  return transforms.concat(sepiaOptions);
};

const blackwhite = (transforms = [], options) => {
  const allowed = [
    { name: 'threshold', type: numberRange(0, 100) },
  ];
  checkOptions('blackwhite', allowed, options);
  const blackWhiteOptions = formatOptions('blackwhite', allowed, options);
  return transforms.concat(blackWhiteOptions);
};

const border = (transforms = [], options) => {
  const allowed = [
    { name: 'width', type: numberRange(1, 1000) },
    { name: 'color', type: t.String },
    { name: 'background', type: t.String },
  ];
  checkOptions('border', allowed, options);
  const borderOptions = formatOptions('border', allowed, options);
  return transforms.concat(borderOptions);
};

const circle = (transforms = [], options) => {
  const allowed = [
    { name: 'background', type: t.String },
  ];
  checkOptions('circle', allowed, options);
  const circleOptions = formatOptions('circle', allowed, options);
  return transforms.concat(circleOptions);
};

const shadow = (transforms = [], options) => {
  const allowed = [
    { name: 'blur', type: numberRange(0, 20) },
    { name: 'opacity', type: numberRange(0, 100) },
    { name: 'vector', type: range(-1000, 1000) },
    { name: 'color', type: t.String },
    { name: 'background', type: t.String },
  ];
  checkOptions('shadow', allowed, options);
  const shadowOptions = formatOptions('shadow', allowed, options);
  return transforms.concat(shadowOptions);
};

const tornEdges = (transforms = [], options) => {
  const allowed = [
    { name: 'spread', type: range(1, 10000) },
    { name: 'background', type: t.String },
  ];
  checkOptions('tornEdges', allowed, options);
  const tornEdgesOptions = formatOptions('tornEdges', allowed, options);
  return transforms.concat(tornEdgesOptions);
};

const polaroid = (transforms = [], options) => {
  const allowed = [
    { name: 'color', type: t.String },
    { name: 'rotate', type: numberRange(0, 359) },
    { name: 'background', type: t.String },
  ];
  checkOptions('polaroid', allowed, options);
  const polaroidOptions = formatOptions('polaroid', allowed, options);
  return transforms.concat(polaroidOptions);
};

const vignette = (transforms = [], options) => {
  const allowed = [
    { name: 'amount', type: numberRange(0, 100) },
    { name: 'blurmode', type: t.enums.of('linear gaussian') },
    { name: 'background', type: t.String },
  ];
  checkOptions('vignette', allowed, options);
  const vignetteOptions = formatOptions('vignette', allowed, options);
  return transforms.concat(vignetteOptions);
};

const roundedCorners = (transforms = [], options) => {
  const allowed = [
    { name: 'radius', type: t.union([numberRange(1, 10000), t.enums.of('max')]) },
    { name: 'blur', type: numberRange(0, 20) },
    { name: 'background', type: t.String },
  ];
  checkOptions('roundedCorners', allowed, options);
  const roundedCornersOptions = formatOptions('roundedCorners', allowed, options);
  return transforms.concat(roundedCornersOptions);
};

const rotate = (transforms = [], options) => {
  const allowed = [
    { name: 'deg', type: t.union([numberRange(0, 359), t.enums.of('exif')]) },
    { name: 'exif', type: t.Boolean },
    { name: 'background', type: t.String },
  ];
  const keys = checkOptions('rotate', allowed, options);
  if (keys.length < 1) {
    throw new Error('Rotate options must contain either deg or exif');
  }

  const rotateOptions = formatOptions('rotate', allowed, options);
  return transforms.concat(rotateOptions);
};

const resize = (transforms = [], options) => {
  const allowed = [
    { name: 'width', type: numberRange(0, 10000) },
    { name: 'height', type: numberRange(0, 10000) },
    { name: 'fit', type: t.enums.of('clip crop scale max') },
    { name: 'align', type: t.union([t.enums.of('center top bottom left right faces'), alignPair]) },
  ];
  const keys = checkOptions('resize', allowed, options);
  if (keys.length < 1) {
    throw new Error('Resize options must contain either width, height, fit, or align');
  }
  const resizeOptions = formatOptions('resize', allowed, options);
  return transforms.concat(resizeOptions);
};

const flip = (transforms = [], value) => {
  if (typeof value !== 'boolean') {
    throw Error('flip must be a Boolean value');
  }
  return value ? transforms.concat('flip') : transforms;
};

const flop = (transforms = [], value) => {
  if (typeof value !== 'boolean') {
    throw Error('flop must be a Boolean value');
  }
  return value ? transforms.concat('flop') : transforms;
};

const monochrome = (transforms = [], value) => {
  if (typeof value !== 'boolean') {
    throw Error('monochrome must be a Boolean value');
  }
  return value ? transforms.concat('monochrome') : transforms;
};

const crop = (transforms = [], options) => {
  const allowed = [
    {
      name: 'dim',
      type: t.struct({
        x: t.Integer,
        y: t.Integer,
        width: t.Integer,
        height: t.Integer,
      }),
    },
  ];
  checkOptions('crop', allowed, options);
  const dim = options.dim;
  const cropOptions = `crop=dim:[${dim.x},${dim.y},${dim.width},${dim.height}]`;
  return transforms.concat(cropOptions);
};

// Table of valid transformers, keys point to private functions
export const transformers = {
  crop,
  resize,
  rotate,
  roundedCorners,
  vignette,
  polaroid,
  tornEdges,
  shadow,
  circle,
  border,
  flip,
  flop,
  blackwhite,
  monochrome,
  sepia,
};

const transform = (session, url, config) => {
  const allowed = Object.keys(transformers);
  // Need some options or we're done here
  const keys = Object.keys(config);
  // Fail fast
  keys.forEach((key) => {
    if (allowed.indexOf(key) < 0) {
      throw new Error(`Invalid option specified: ${key} is not a valid transform option.`);
    }
  });
  // Construct URL
  const construct = (arr = [], count = 0) => {
    if (count === keys.length) {
      return arr;
    }
    const transformIndex = keys[count];
    const transformConfig = config[transformIndex];
    const transformer = transformers[transformIndex];
    const newCount = count + 1;
    return construct(transformer(arr, transformConfig), newCount);
  };

  if (session.policy && session.signature) {
    const secString = `security=policy:${session.policy},signature:${session.signature}`;
    const baseURL = `${processURL}/${session.apikey}/${secString}`;
    const transformString = construct().join('/');
    return `${baseURL}/${transformString}/${url}`;
  }

  // See URL format: https://www.filestack.com/docs/image-transformations
  const baseURL = `${processURL}/${session.apikey}`;
  const transforms = construct();
  if (transforms.length) {
    const transformString = construct().join('/');
    return `${baseURL}/${transformString}/${url}`;
  }
  return url;
};

export default transform;
