/* eslint-disable max-len */
import storeURL from './store';
import { retrieve, remove, metadata } from './file';
import cloud from './cloud';
import transform from './transform';
import upload from './uploader-browser';

/**
 * @module filestack
 */

/**
 * @typedef TransformOptions {object}
 * @param crop {object} - [Crop options.](https://www.filestack.com/docs/image-transformations/crop)
 * @param crop.dim {object} - Crop dimensions.
 * @param crop.dim.x {number}
 * @param crop.dim.y {number}
 * @param crop.dim.width {number}
 * @param crop.dim.height {number}
 * @param resize {object} - [Resize options.](https://www.filestack.com/docs/image-transformations/resize) At least one option is __required__.
 * @param resize.width {number}
 * @param resize.height {number}
 * @param resize.fit {string} - One of `clip`, `crop`, `scale`, `max`.
 * @param resize.align {string} - One of `center`, `top`, `bottom`, `left`, `right`, `faces`, or align pair like `['top', 'left']`.
 * @param rotate {object} - [Rotate options](https://www.filestack.com/docs/image-transformations/rotate). At least one option is __required__.
 * @param rotate.deg {number|string} - Can be number in range 0-359 or `exif`.
 * @param rotate.exif {boolean}
 * @param rotate.background {string}
 * @param flip {boolean} - [Flip](https://www.filestack.com/docs/image-transformations/rotate#flip) image
 * @param flop {boolean} - [Flop](https://www.filestack.com/docs/image-transformations/rotate#flop) image
 * @param roundedCorners {object} - [Rounded corners options.](https://www.filestack.com/docs/image-transformations/borders-and-effects#rounded-corners)
 * @param roundedCorners.radius {number|string} - Can be number in range 1-10000 or `max`.
 * @param roundedCorners.blur {number}
 * @param roundedCorners.background {string}
 * @param vignette {object} - [Vignette options.](https://www.filestack.com/docs/image-transformations/borders-and-effects#vignette)
 * @param vignette.amount {number}
 * @param vignette.blurmode {string} - One of `linear` or `gaussian`.
 * @param vignette.background  {string}
 * @param polaroid {object} - [Polaroid options.](https://www.filestack.com/docs/image-transformations/borders-and-effects#polaroid)
 * @param polaroid.color {string}
 * @param polaroid.rotate {number}
 * @param polaroid.background {string}
 * @param tornEdges {object} - [Torn edges options.](https://www.filestack.com/docs/image-transformations/borders-and-effects#torn-edges)
 * @param tornEdges.spread {number[]} - Range format `[10, 50]`.
 * @param tornEdges.background {string}
 * @param shadow {object} - [Shadow options.](https://www.filestack.com/docs/image-transformations/borders-and-effects#shadow)
 * @param shadow.blur {number}
 * @param shadow.opacity {number}
 * @param shadow.vector {number[]} - Range format `[25, 25]`.
 * @param shadow.color {string}
 * @param shadow.background {string}
 * @param circle {object} - [Circle options.](https://www.filestack.com/docs/image-transformations/borders-and-effects#circle)
 * @param circle.background {string}
 * @param border {object} - [Border options.](https://www.filestack.com/docs/image-transformations/borders-and-effects#border)
 * @param border.width {number}
 * @param border.color {number}
 * @param border.background {number}
 * @param monochrome {boolean} - [Monochrome.](https://www.filestack.com/docs/image-transformations/filters#monochrome)
 * @param sepia {object} - [Sepia.](https://www.filestack.com/docs/image-transformations/filters#sepia)
 * @param sepia.tone {number}
 */

/**
 * @callback progressCallback
 * @param event {object} - Progress event.
 * @param event.totalProgressPercent {number} - Percent of total upload.
 * @param event.progressTotal {number} - Total number of bytes uploaded thus far across all parts.
 * @param event.part {number} - Part #.
 * @param event.loaded {number} - Amount of data in this part that has been uploaded.
 * @param event.byteLength {number} - Total number of bytes in this part.
 */

/**
 * @callback retryCallback
 * @param attempt {number} - Which attempt the upload is currently on.
 * @param nextAttempt {number} - ms before the attempt will start
 */

/**
 * Initializes the client.
 * @alias module:filestack~init
 * @typicalname client
 * @param apikey {string} - Filestack API key. Get a free key [here](https://dev.filestack.com/register/free).
 * @param [security] {object} - Read about [security policies](https://www.filestack.com/docs/security).
 * @param security.policy {string} - Filestack security policy encoded in base64.
 * @param security.signature {string} - HMAC-SHA256 sIgnature for the security policy.
 * @returns {object} Object containing the available methods documented below.
 * @example
 *
 * import apiClient from 'api-client';
 * const client = apiClient.init('apikey');
 */
const init = (apikey, security) => {
  if (!apikey || typeof apikey !== 'string') {
    throw new Error('apikey is required to initialize the Filestack client');
  }

  if (security && !(security.policy && security.signature)) {
    throw new Error('signature and policy are both required for security');
  }

  const session = Object.assign({}, security, { apikey });

  return {
    /**
     * Get current security parameters
     * @memberof module:filestack~init
     * @returns {object} Object containing current security parameters
     */
    getSecurity: () => {
      return {
        policy: session.policy,
        signature: session.signature,
      };
    },
    /**
     * Set security parameters -- useful for changing policy on instantiated client
     * @memberof module:filestack~init
     * @param security {object} - Read about [security policies](https://www.filestack.com/docs/security).
     * @param security.policy {string} - Filestack security policy encoded in base64.
     * @param security.signature {string} - HMAC-SHA256 sIgnature for the security policy.
     * @returns {object} Object containing current session parameters
     *
     * @example
     *
     * client.setSecurity({ policy: 'policy', signature: 'signature' });
     */
    setSecurity(sec) {
      if (sec && !(sec.policy && sec.signature)) {
        throw new Error('signature and policy are both required for security');
      }
      session.policy = sec.policy;
      session.signature = sec.signature;
      return session;
    },
    /**
     * Interface to the Filestack [Store API](https://www.filestack.com/docs/rest-api/store). Used for storing from a URL.
     * @memberof module:filestack~init
     * @param url {string} - Valid URL to a file.
     * @param [options] {object}
     * @param options.filename {string}
     * @param options.location=s3 {string} - One of `s3`, `gcs`, `rackspace`, `azure`, `dropbox`.
     * @param options.mimetype {string}
     * @param options.path {string}
     * @param options.container {string}
     * @param options.region {string} - Valid S3 region for the selected container (S3 only).
     * @param options.access {string} - One of `public` or `private`.
     * @returns {Promise}
     * @fulfil {object} - Metadata of stored file.
     * @reject {error} - A Superagent error object.
     *
     * @example
     *
     * client
     *   .storeURL('https://d1wtqaffaaj63z.cloudfront.net/images/NY_199_E_of_Hammertown_2014.jpg')
     *   .then(res => console.log(res));
     */
    storeURL(url, uploadOptions) {
      return storeURL(session, url, uploadOptions);
    },
    /**
     * Interface to the Filestack [Retrieve API](https://www.filestack.com/docs/rest-api/retrieve).
     * Used for accessing files via Filestack handles.
     * @memberof module:filestack~init
     * @param handle {string} - Valid Filestack handle.
     * @param [options] {object}
     * @param options.metadata {boolean} - return json of file metadata
     * @param options.head {boolean} - perform a 'head' request instead of a 'get'
     * @param options.dl {boolean} - X-File-Name will be returned
     * @param options.extension {string} - add extension to handle
     * @returns {Promise}
     * @fulfil {object} - Metadata of stored file or stored file, depending on metadata / head option.
     * @reject {error} - A Superagent error object.
     *
     * @example
     *
     * client
     *   .retrieve('DCL5K46FS3OIxb5iuKby')
     *   .then((blob) => {
     *      var urlCreator = window.URL || window.webkitURL;
     *      var imageUrl = urlCreator.createObjectURL(blob);
     *      document.querySelector('#myImage').src = imageUrl;
     *   })
     *   .catch((err) => {
     *      console.log(err);
     *   }));
     */
    retrieve(handle, uploadOptions) {
      return retrieve(session, handle, uploadOptions);
    },
    /**
     * Interface to the Filestack [Remove API](https://www.filestack.com/docs/rest-api/remove).
     * Used for removing files, __requires security to be enabled__.
     * @memberof module:filestack~init
     * @param handle {string} - Valid Filestack handle.
     * @returns {Promise}
     * @fulfil {object} - Result of remove.
     * @reject {error} - A Superagent error object.
     *
     * @example
     *
     * client
     *   .remove('DCL5K46FS3OIxb5iuKby')
     *   .then((res) => {
     *     console.log(res);
     *   })
     *   .catch((err) => {
     *     console.log(err);
     *   }));
     */
    remove(handle, options) {
      return remove(session, handle, options);
    },
    /**
     * Interface to the Filestack [Metadata API](https://www.filestack.com/docs/rest-api/meta-data).
     * Used for retrieving detailed data of stored files.
     * @memberof module:filestack~init
     * @param handle {string} - Valid Filestack handle.
     * @param [options] {object}
     * @param options.size {boolean}
     * @param options.mimetype {boolean}
     * @param options.filename {boolean}
     * @param options.width {boolean}
     * @param options.height {boolean}
     * @param options.uploaded {boolean}
     * @param options.writeable {boolean}
     * @param options.cloud {boolean}
     * @param options.sourceUrl {boolean}
     * @param options.md1 {boolean}
     * @param options.sha1 {boolean}
     * @param options.sha224 {boolean}
     * @param options.sha256 {boolean}
     * @param options.sha384 {boolean}
     * @param options.sha512 {boolean}
     * @param options.location {boolean}
     * @param options.path {boolean}
     * @param options.container {boolean}
     * @param options.exif {boolean}
     * @returns {Promise}
     * @fulfil {object} - Result of metadata.
     * @reject {error} - A Superagent error object.
     *
     * @example
     *
     * client
     *   .metadata('DCL5K46FS3OIxb5iuKby')
     *   .then((res) => {
     *     console.log(res);
     *   })
     *   .catch((err) => {
     *     console.log(err);
     *   }));
     */
    metadata(handle, options) {
      return metadata(session, handle, options);
    },
    /**
     * Initializes a CloudRouter client. (Internal only).
     * @ignore
     * @memberof module:filestack~init
     * @param name {string} - Name of cloud source, e.g. `dropbox`, `imagesearch`.
     */
    cloud(name) {
      return cloud(session, security, name);
    },
    /**
     * Interface to the Filestack [transformation engine](https://www.filestack.com/docs/image-transformations).
     * @memberof module:filestack~init
     * @param url {string} - Valid URL to an image.
     * @param options {module:filestack~TransformOptions} - Transformations are applied in the order specified by this object.
     * @returns {string} A new URL that points to the transformed resource.
     * @example
     *
     * const transformedUrl = client.transform(url, {
     *   crop: {
     *     dim: {
     *       x: 0,
     *       y: 50,
     *       width: 300,
     *       height: 300,
     *     },
     *   },
     *   vignette: {
     *     blurmode: 'gaussian',
     *     amount: 50,
     *   },
     *   flip: true,
     * };
     *
     * // optionally store the new URL
     * client.storeURL(transformedUrl).then(res => console.log(res));
     */
    transform(url, options) {
      return transform(session, url, options);
    },
    /**
     * Initiates a multi-part upload flow.
     * @memberof module:filestack~init
     * @param file {File} - must be a valid [File](https://developer.mozilla.org/en-US/docs/Web/API/File) or Blob.
     * @param [uploadOptions] {object}
     * @param [uploadOptions.partSize=5 * 1024 * 1024] {number} - Size of each uploaded part.
     * @param [uploadOptions.maxConcurrentUploads=5] {number} - Max number of concurrent uploads.
     * @param [uploadOptions.retryOptions] {object} - params for retry settings
     * @param [uploadOptions.retryOptions.retries=10] {number} - max number of retries
     * @param [uploadOptions.retryOptions.factor=2] {number} - the exponential factor to use
     * @param [uploadOptions.retryOptions.minTimeout=1 * 1000] {number} - ms before starting first retry
     * @param [uploadOptions.retryOptions.maxTimeout=60 * 1000] {number} - max ms between two retries
     * @param [uploadOptions.onStart] {function} -  Called when the flow begins (before Filestack handshake request is made).
     * @param [uploadOptions.onUploadStart] {function} - Called when an upload begins (after S3 request is made).
     * @param [uploadOptions.onProgress] {module:filestack~progressCallback} - Called on progress event.
     * @param [uploadOptions.onRetry] {module:filestack~retryCallback} - Called if upload fails and retry is occurring
     * @param [uploadOptions.onUploadComplete] {function} - Called when an upload is completing (before final completion request).
     * @param [storeOptions] {object} - Configure where the file is stored.
     * @param [storeOptions.location] {string} - Valid options are: `s3`, `gcs`, `dropbox`, `azure`, `rackspace`.
     * @param [storeOptions.region] {string} - Valid S3 region for the selected container (S3 only).
     * @param [storeOptions.container] {string} - Name of the storage container.
     * @param [storeOptions.path] {string} - Path where the file will be stored. A trailing slash will put the file in that folder path.
     * @param [storeOptions.access] {string} - Valid options are `private` or `public`.
     * @param [token] {object} - A token that can be used to call cancel, pause, and resume
     *
     * @returns {Promise}
     * @fulfil {object} - Metadata of uploaded file.
     * @reject {error} - An error object depending on where the flow halted.
     * @example
     *
     * const token = {};
     * client.upload(file, uploadOptions, storeOptions, token)
     *   .then(res => console.log(res));
     * token.pause();
     * token.resume();
     * token.cancel();
     */
    upload(file, options, storeOptions, token) {
      return upload(session, file, options, storeOptions, token);
    },
  };
};

export default {
  /**
   * Gets current version.
   *
   * @alias module:filestack~version
   * @static
   * @returns {string}
   * @example
   *
   * import apiClient from 'api-client';
   * console.log(apiClient.version);
   */
  version: '@{VERSION}',
  init,
};
